import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
    CapsuleTabs,
    Selector,
    Toast,
    Grid,
} from "antd-mobile";
import {useContext, useState} from "react";
import BasePage from "./BasePage";

import Settings, {
    BlackEnum,
    ColorContext,
    ColorType,
    DaynightContext,
    SettingsContext,
    ThemeColor,
    ThemeContext
} from './Settings'
import settings from "./Settings";

export default function MathPage({onBack}) {
    const {settings, setSettings} = useContext(SettingsContext)
    const [operator, setOperator] = useState(['+']);
    const [tab, setTab] = useState('+');
    return <BasePage
        naviTitle={'数学'}
        onBack={onBack}
        naviRight={undefined}
    >
        <div
            style={{
                flex: 1, backgroundColor: settings.color[1],
                width: '100%',
            }}
        >
            <div
                style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    padding: 15,
                    borderBottomStyle: 'solid',
                    borderBottomWidth: 1,
                    borderBottomColor: settings.black[BlackEnum.Border],
                }}
            >
                <Selector
                    style={{
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                    options={[
                        {label: '加', value: '+'},
                        {label: '减', value: '-'},
                        {label: '乘', value: '×'},
                        {label: '除', value: '÷'},
                    ]}
                    value={operator}
                    multiple={true}
                    onChange={(arr, extend) => {
                        if (arr.length != 0) {
                            setOperator(arr)
                            if (arr.indexOf('÷') >= 0) {
                                setTab('÷')
                            } else if (arr.indexOf('×') >= 0) {
                                setTab('×')
                            } else if (arr.indexOf('-') >= 0) {
                                setTab('-')
                            } else if (arr.indexOf('+') >= 0) {
                                setTab('+')
                            }
                        }
                    }}
                />
            </div>
            <CapsuleTabs
                activeKey={tab}
                onChange={(key) => {
                    setTab(key)
                }}
            >
                {operator.indexOf('+') >= 0 ? <CapsuleTabs.Tab
                    title='加法'
                    key='+'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'+'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('-') >= 0 ? <CapsuleTabs.Tab
                    title='减法'
                    key='-'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'-'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('+') >= 0 && operator.indexOf('-') >= 0 ? <CapsuleTabs.Tab
                    title='加减法'
                    key='+-'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                >
                    5
                </CapsuleTabs.Tab> : null}
                {operator.indexOf('×') >= 0 ? <CapsuleTabs.Tab
                    title='乘法'
                    key='×'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'*'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('÷') >= 0 ? <CapsuleTabs.Tab
                    title='除法'
                    key='÷'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'/'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('×') >= 0 && operator.indexOf('÷') >= 0 ? <CapsuleTabs.Tab
                    title='乘数法'
                    key='×÷'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                >
                    6
                </CapsuleTabs.Tab> : null}
                {operator.indexOf('+') >= 0 && operator.indexOf('-') >= 0
                && operator.indexOf('×') >= 0 && operator.indexOf('÷') >= 0 ?
                    <CapsuleTabs.Tab
                        title='加减乘除法'
                        key='+-×÷'
                        style={{
                            maxWidth: 120,
                            fontSize: Settings.getFont(1)[0],
                            lineHeight: Settings.getFont(1)[1],
                        }}
                    >
                        7
                    </CapsuleTabs.Tab> : null}
            </CapsuleTabs>
        </div>
    </BasePage>
}

function getRan(config, isDivide = false) {
    let r = parseInt(config.base + Math.random() * config.num % config.num)
    while (isDivide && r == 0) {
        r = parseInt(config.base + Math.random() * config.num % config.num)
    }
    return r
}

function renderDot(num) {
    let arr = []
    for (let i = 0; i < num; i++) {
        arr.push(0)
    }
    let size = window.innerWidth - 24 - 15 * 2 - 4 * 10
    size /= 8
    size -= 4
    return <Grid
        gap={8}
        columns={2}
        style={{
            width: '100%',
        }}>
        {arr.map(n => {
            return <Grid.Item
                span={1}
                style={{
                    width: '100%',
                }}
            >
                <div style={{
                    width: size, height: size,
                    borderRadius: size / 2,
                    backgroundColor: ThemeColor['Green'][0][5],
                }}/>
            </Grid.Item>
        })}
    </Grid>
}

function Operation1({operator}) {
    const [showSelection, setShowSelection] = useState(false);
    const {settings, setSettings} = useContext(SettingsContext)
    const [stage, setStage] = useState(0)
    const [a, setA] = useState(getRan(NumberStage[stage]));
    const [b, setB] = useState(getRan(NumberStage[stage], operator == '/'));
    const [result, setResult] = useState(parseInt(eval(a + operator + b)));
    const [answer, setAnswer] = useState(null);
    const [answerSelection, setAnswerSelection] = useState([]);
    const [correct, setCorrect] = useState(0)
    const [totalCorrect, setTotalCorrect] = useState(0)
    const [totalWrong, setTotalWrong] = useState(0)
    return <div style={{}}>
        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
                paddingLeft: 15, paddingRight: 15,
            }}
        >
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Magenta'][0][5],
                marginRight: 20,
            }}>当前难度：{NumberStage[stage].title}</span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: settings.color[9],
                marginRight: 4,
            }}>答题进度：
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                color: settings.color[9],
                marginRight: 4,
            }}>{correct}/
                <span style={{
                    fontSize: Settings.getFont(4)[0],
                    lineHeight: Settings.getFont(4)[1],
                    color: ThemeColor['Blue'][0][5],
                }}>10</span>
            </span>
            </span>
        </div>
        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
                paddingLeft: 15, paddingRight: 15,
            }}
        >
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Green'][0][5],
                marginRight: 4,
            }}>答对题数：
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                marginRight: 10,
            }}>{totalCorrect}</span>
            </span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Red'][0][5],
                marginRight: 4,
            }}>答错题数：
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                marginRight: 20,
            }}>{totalWrong}</span>
            </span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Yellow'][0][5],
                marginRight: 4,
            }}>答题百分比：{totalWrong || totalWrong ? parseInt(totalCorrect / (totalWrong + totalCorrect) * 100) : 0}%</span>

        </div>
        <Grid
            columns={8}
            gap={10}
            style={{
                padding: 15,
            }}
        >
            <Grid.Item
                span={2}
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    // justifyContent: 'center',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        borderRadius: 6,
                        borderStyle: 'solid',
                        borderWidth: 1,
                        borderColor: settings.black[BlackEnum.Border],
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        height: 40,
                    }}
                >
                     <span style={{
                         fontSize: Settings.getFont(2)[0],
                         lineHeight: Settings.getFont(2)[1],
                         color: settings.color[9],
                     }}>{a}</span>
                </div>
                <div
                    style={{
                        marginTop: 20,
                        width: '100%', height: 400,
                    }}
                >{renderDot(a)}</div>
            </Grid.Item>
            <Grid.Item
                span={1}
                style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    height: 40,
                    justifyContent: 'center',
                }}
            >
                <span style={{
                    fontSize: Settings.getFont(5)[0],
                    lineHeight: Settings.getFont(5)[1],
                    fontWeight: 'bold',
                    color: settings.color[9],
                }}>{operator == '*' ? '×' : operator == '/' ? '÷' : operator}</span>
            </Grid.Item>
            <Grid.Item
                span={2}
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        borderRadius: 6,
                        borderStyle: 'solid',
                        borderWidth: 1,
                        borderColor: settings.black[BlackEnum.Border],
                        display: 'flex',
                        flexDirection: 'row',
                        alignItems: 'center',
                        justifyContent: 'center',
                        height: 40,
                    }}
                >
                 <span style={{
                     fontSize: Settings.getFont(2)[0],
                     lineHeight: Settings.getFont(2)[1],
                     color: settings.color[9],
                 }}>{b}</span>
                </div>
                <div
                    style={{
                        marginTop: 20,
                        width: '100%', height: 400,
                    }}
                >{renderDot(b)}</div>
            </Grid.Item>
            <Grid.Item
                span={1}
                style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'center',
                    height: 40,
                }}
            >
                <span style={{
                    fontSize: Settings.getFont(5)[0],
                    lineHeight: Settings.getFont(5)[1],
                    fontWeight: 'bold',
                    color: settings.color[9],
                }}>=</span>
            </Grid.Item>
            <Grid.Item span={2}>
                <Button
                    style={{
                        width: '100%',
                        borderRadius: 6,
                        // borderStyle: 'solid',
                        // borderWidth: 1,
                        // borderColor: settings.black[BlackEnum.Border],
                        // borderColor: settings.color[9],
                        display: 'flex',
                        flexDirection: 'row',
                        alignItems: 'center',
                        justifyContent: 'center',
                        height: 40,
                        backgroundColor: settings.color[9],
                    }}
                    onClick={() => {
                        setShowSelection(true)
                    }}
                >
                 <span style={{
                     fontSize: Settings.getFont(4)[0],
                     lineHeight: Settings.getFont(4)[1],
                     color: settings.color[1],
                 }}>{answer == undefined ? '?' : answer}</span>
                </Button>
            </Grid.Item>
        </Grid>
        <Popup
            maskStyle={{
                '--background-color': settings.black[BlackEnum.Background]
            }}
            closeOnMaskClick={true}
            visible={showSelection}
            onMaskClick={() => {
                setShowSelection(false)
            }}
            onClose={() => {
                setShowSelection(false)
            }}
            bodyStyle={{
                maxHeight: '80vh',
                display: 'flex', flexDirection: 'column',
                alignItems: 'center',
                backgroundColor: settings.color[4],
                overflowY: 'auto',
            }}
        >
            <div
                style={{
                    width: '100%', height: '100%',
                    display: 'flex', flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <span style={{
                    marginTop: 15,
                    fontSize: Settings.getFont(4)[0],
                    lineHeight: Settings.getFont(4)[1],
                    color: settings.black[BlackEnum.PrimaryText]
                }}>请选择答案</span>
                <div
                    style={{
                        flex: 1, width: '100%', marginTop: 15, marginBottom: 20,
                    }}
                >
                    <Selector
                        value={answerSelection}
                        style={{
                            paddingLeft: 20, paddingRight: 20,
                        }}
                        options={AllValueSelector}
                        onChange={(arr, extend) => {
                            setAnswer(arr[0])
                            setShowSelection(false)
                            if (arr[0] == result) {
                                setAnswerSelection([])
                                let tc = totalCorrect + 1
                                setTotalCorrect(tc)
                                let c = correct + 1
                                //答对10条后升级难度 后续可考虑动态配置
                                let s = Math.min(stage + (c >= 10 ? 1 : 0), NumberStage.length - 1)
                                setStage(s)
                                if (c >= 10) {
                                    c = 0
                                }
                                setCorrect(c)
                                let _a = getRan(NumberStage[s],);
                                let _b = getRan(NumberStage[s], operator == '/')
                                setA(_a)
                                setB(_b)
                                setResult(parseInt(eval(_a + operator + _b)))
                                setAnswer(null)
                                Toast.show('回答正确')
                            } else {
                                setAnswerSelection(arr)
                                let tw = totalWrong + 1
                                setTotalWrong(tw)
                                setCorrect(0)
                                Toast.show('回答错误')
                            }
                        }}
                    />
                </div>
            </div>
        </Popup>
    </div>
}

const NumberStage = [
    {
        base: 0,
        num: 5,
        title: '0-5',
    },
    {
        base: 5,
        num: 5,
        title: '6-10'
    },
    {
        base: 10,
        num: 5,
        title: '11-15'
    },
    {
        base: 15,
        num: 5,
        title: '16-20'
    },
]
let PlusSelector = []
for (let i = 0; i < 10 + 10; i++) {
    PlusSelector.push({
        label: i.toString(),
        value: i.toString(),
    })
}
let AllValueSelector = []
for (let i = 0; i < 10 * 10 + 1; i++) {
    AllValueSelector.push({
        label: i.toString(),
        value: i.toString(),
    })
}
